.catalog {
  margin-top: 28px;
  margin-bottom: 20px;
  font-size: 14px;
  &_animate {
    transition: opacity .3s ease-in-out;
    -webkit-transition: opacity .3s ease-in-out;
    -o-transition: opacity .3s ease-in-out;
    opacity: 0;
    visibility: hidden;
  }
  @media @minMobile {
    &__view {
      cursor: pointer;
      background-color: rgba(255,255,255,.8);
      border: solid 1px @lGrey;
      text-align: center;
      right: auto;
      width: 188px;
      height: 38px;
      position: absolute;
      vertical-align: middle;
      line-height: 38px;
      top: 50%;
      left: 50%;
      margin-left: -94px;
      margin-top: -19px;
    }
  }
  &__wrap {
    position: relative;
    width: 285px;
    height: 355px;
    @media @minMobile {
      &:hover {
        .catalog_animate {
          opacity: 1;
          visibility: visible;
        }
      }
      &:hover {
        .catalog__wrap_bd {
          margin-top: -1px;
          position: absolute;
          padding-left: 68px;
          padding-right: 14px;
          left: -54px;
          top: 0;
          padding-bottom: 21px;
          min-height: 100%;
          width: 339px;
          border: solid 1px @lGrey;
        }
      }
    }
  }
  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 255px;
    position: relative;
  }
  &__title {
    display: block;
    margin-top: 17px;
    margin-bottom: 3px;
    font-weight: bold;
    &_link {
      display: block;
      text-decoration: underline;
    }
    &_l {
      font-size: 20px;
    }
  }
  &__description {
    min-height: 38px;
    display: block;
    color: @lGrey;
    line-height: 19px;
    margin-bottom: 8px;
  }
  &__price {
    color: @orange;
  }
  &__label {
    background-color: @orange;
    border-radius: 3px;
    font-size: 12px;
    color: #fff;
    padding: 2px 7px;
    height: 19px;
  }
  &__hover {
    .clearfix;
    @media @minMobile {
      &__image {
        position: absolute;
        left: 0;
        top: 0;
        padding-left: 9px;
        padding-top: 10px;
        background-color: #fff;
        min-height: 100%;
        li {
          width: 49px;
          height: 64px;
          border: solid 2px @orange;
          text-align: center;
          line-height: 60px;
          overflow: hidden;
          margin-bottom: 10px;
        }
        img {
          width: auto;
          height: 80%;
          vertical-align: -19px;
        }
      }
    }
    &__action {
      margin-top: 14px;
      .link {
        display: block;
        &:first-child {
          margin-bottom: 7px;
        }
        &:hover .icon {
          fill: @orange;
        }
      }
      .icon {
        fill: @lGrey;
        display: inline-block;
        font-size: 1.4em;
        margin-right: 5px;
        vertical-align: middle;
      }
      .favorite_select,
      .compare_select {
        .icon {
          fill: @orange;
        }
      }
    }
  }
}